@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html, body {
  height: 100%;
  background-color: #006C54;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.temsSplash {
  opacity:1;     
  transition: visibility 0.8s linear, opacity 0.8s linear;
  background: #006C54;
  height: 100%;
  width: 100%;
  left:0;
  top:0;
  position: fixed;
  z-index: 1;
  overflow-x: hidden;
}

.App{
  background-color: #006C54;
}

.fade {
  opacity:0;
  visibility:hidden;
}

.logo {
  width: 40%;
  margin-left:30%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.recruitmentResults {
    text-align: center;
    padding: 0 0 10vh 0;
}

.recruitmentResultsButton {
    font-family: 'Lato' !important;
    padding: 1%;
    color: black !important;
    background-color: white !important;
}

.resultsLink {
    text-decoration: none;
}

body {
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}

@media only screen and (orientation:landscape){  
    .recruitmentResults {
        text-align: center;
        padding: 0 0 5vw 0;
    }
}
.onBoard {
    position: relative;
    height:100%;
    width:100%;
    text-align: center;
    margin:0;
    font-family: 'Poppins';
}

.imgOnBoard {
    height:50vw;
    padding: 15vh 0 0 0;
}

.caption {
    font-size: 7vh;
    margin: 10vw 0 0 0;
    padding: 0 0 20vh 0;
}

.control-left {
    position: absolute;
    top:75%;
    left:20%;
}

.control-right {
    position: absolute;
    top:75%;
    right: 20%;

}

@media only screen and (orientation:landscape){
    .imgOnBoard {
        height:20vw;
        padding: 15vh 0 0 0 ;
    }

    .caption {
        font-size: 8vw;
        margin: 1vw 0 0 0;
        padding:0 0 2vw 0;
    }

    .control-left {
        position: absolute;
        top:50%;
        left:5%;
    }
    
    .control-right {
        position: absolute;
        top:50%;
        right:5%;
 
    }
}
.navbar{
    padding: 7vh 0 3vh 0;
}

.link{
    text-decoration: none;
    color: white !important;
    font-family: 'Lato';
}

.link:hover, .link:active{
    color: white;
    cursor: pointer;
    padding-bottom: 5px;
    border-bottom: 4px solid white;
}


.aboutContainer {
    margin: 0;
    text-align: center;
    padding: 0 0 5vh 0;
}

.navLogo {
    width: 20vh;
    padding:5vh 0 5vh 0;
}   

.bannerContainer {
    color: white;
}

.bannerTitle {
    font-family: 'Montserrat';
    letter-spacing: 0.05em;
}

.bannerCaption {
    font-family: 'Lato';
    letter-spacing: 0.05em;
    line-height: 1.6em;
}

@media only screen and (orientation:landscape){    
    .navLogo {
        width: 40vh;
        padding:10vh;
    }   
}
.statsContainer {
    background-color: white;
    padding: 10vh 0 5vh 0;
    text-align: center;
    font-family: "Lato";
    letter-spacing: 0.05em;
    color:#0E141E
}

.content {
    font-size: 84%;
}

.statItem {
    padding: 0 0 5vh 0;
}

.section-heading {
    font-size: 1.7em;
    padding: 0 0 5vh 0;
}

.sub-heading { 
    font-size: 1.5em;
    padding: 0 0 2vh 0;
}

.counter {
    font-family: 'Poppins';
    font-size: 2.5em;
}

@media only screen and (orientation:landscape){
    .statsContainer {
        background-color: white;
        padding: 8vw 0 8vw 0;
    }
    .section-heading {
        font-size: 1.7em;
        padding: 0 0 5vw 0;
    }
}
.domainChip{
    margin: 1% 0.5% 0 0.5%;
    color: white !important;
    font-family: 'Lato' !important;
    letter-spacing: 0.01em;
}

.tech {
    background-color: #023d32 !important;
}

.mgmt {
    background-color:  #006650f3 !important;
}

.creative {
    background-color:  #058065a3 !important;
}

.contentWriting {
    background-color:  #94C997 !important;
}

.domainChip:hover {
    box-shadow: 0 8px 12px 0 rgba(0, 0, 0, 0.2), 0 10px 25px 0 rgba(0, 0, 0, 0.19);
}

.domainLegend{
    padding:5vh 0 0 0;
    text-align: center;
    color:#0E141E;
    font-weight: 600
}
.domainContainer {
    padding: 10vh 0 10vh 0;
    background-color: white;
    text-align: center;
    font-family: 'Lato';
}

@media only screen and (orientation:landscape){   
    .domainContainer {
        padding: 8vw 0 8vw 0;
        background-color: white;
    }
    .domainLegend{
        padding: 3vw 0 0 0;
        text-align: center;
        color:#0E141E
    }
}
.boardContainer {
    padding: 13vh 0 15vh 0;
}

.boardRow {
    padding: 0;
}

.facultyRow {
    padding: 10vh 0 0 0;
}

.boardItem {
    padding: 2vh 0 2vh 0;
}

.boardImage {
    width: 100%;
    height: auto;
    border-radius: 50%;
    display: block;
}

.overlay {
    position: absolute;
    top:0;
    bottom: 0;
    left:0;
    right:0;
    width: 100%;
    opacity: 0;
    height: 100%;
    transition: .5s ease;
    background-color: #0e76a8;
    border-radius:50%;
}

.linkedInIcon {
    position: absolute;
    opacity: 1 !important;
    width: 25% !important;
    height: auto !important;
    top:50%;
    left:50%;
    transform: translate(-50%, -50%);
}

.itemContainer {
    position: relative;
    border-radius: 50%;
}

.itemContainer:hover .overlay {
    opacity: 0.7;
    cursor: pointer;
    border-radius:50%;
}

.itemSubText {
    padding: 2vh 0 0 0;
    text-align: center;
    font-family: 'Lato';
}

.memberName {
    color: white;
    font-weight: 600;
    letter-spacing: 0.06em;
}

.memberDesignation {
    color: #E3E6DA;
    font-size: 80%;
    padding:3%;
    letter-spacing: 0.05em;
}   

.head {
    font-family: 'Poppins';
    text-align: center;
    color: white;
}

.subhead {
    font-family: 'Lato';
    color: white;
    text-align: center;
}

@media only screen and (orientation:landscape){  
    .boardContainer {
        padding: 8vw 0 8vw 0;
    }
    .boardItem {
        padding: 0;
    }
    .boardRow {
        padding: 2vw;
    }   
    .facultyRow {
        padding: 4vw;
    }    
}

.eventsContainer {
    padding: 10vh 0 10vh 0;
    background-color: white;
    text-align: center;
    font-family: 'Lato';
}

.eventItem {
    padding: 0 0 5vh 0;
}

.moreButton {
    background-color:  #006650f3 !important;
    color: white !important;
    font-family: 'Lato' !important;
    margin: 3vh 0 0 0 !important;
}

.moreButton:hover {
    cursor: pointer;
    box-shadow: 0 8px 12px 0 #00665079, 0 10px 25px 0 #00665079;
}

@media only screen and (orientation:landscape){
    .eventsContainer { 
        padding: 3vw 0 6vw 0;
        background-color: white;
    }
    .eventItem {
        padding: 0 0 2vw 0;
    }
}
.successMessage{
    font-weight: bolder;
    font-size: 120%;
}

.submitButton {
    font-family: 'Lato' !important;
    background-color: white !important;
    font-weight: bold !important;
}

.contactHead {
    font-family: 'Poppins';
    text-align: center;
    padding: 5vh;
    font-size: 5vh;
    color:white;
}

.bottomNav {
    padding: 10vh 0vh 7vh 0;
}

.credits {
    font-size: 80%;
    padding-bottom: 7vh;
    text-align: center;
}
.resultsContainer {
    padding: 5vh 0 10vh 0;
    background-color: #006C54;
    height: 100%;
    text-align: center;
}

.resultHead {
    font-family: 'Montserrat';
    letter-spacing: 0.05em;
    color:white;
}

.resultSubHead {
    font-family: 'Lato';
    letter-spacing: 0.05em;
    line-height: 1.6em;
    color:white;
}

.logoSmall {
    width: 20vh;
    padding: 0 0 5vh 0;
}

th, td {
    font-family: 'Lato' !important;
}

.resultTable {
    padding: 0 0 5vh 0;
}

th {
    font-weight: bold !important;
}

@media only screen and (orientation: landscape) {    
    .logoSmall {
        width: 15vw;
        padding: 0 0 2vw 0;
    }

    .resultTable {
        padding: 0 0 2vw 0;
    }
}
